@font-face {
  font-family: 'raleway-light';
  src: url("../fonts/raleway-light-webfont.eot");
  /* IE9 Compat Modes */
  src: url("../fonts/raleway-light-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/raleway-light-webfont.woff2") format("woff2"), url("../fonts/raleway-light-webfont.woff") format("woff"), url("../fonts/raleway-light-webfont.ttf") format("truetype"), url("../fonts/raleway-light-webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
}

@font-face {
  font-family: 'sans-serif';
}

.articulo {
  margin-top: 25px;
}

.articulo h2 {
  background: #efefef;
  margin: 0;
  padding: 5px 0 5px 20px;
}

.articulo img {
  width: 100%;
  height: auto;
  margin: 10px 0 25px 0;
}

.articulo-title {
  background: #d41f1f;
  padding: 10px;
}

.articulo-title a {
  font-size: 36px;
  color: white;
}

@media only screen and (min-width: 769px) {
  .articulo-title a {
    margin-left: 50px;
  }
}

.articulo-title h1 {
  font-size: 36px;
  color: white;
}

@media only screen and (min-width: 769px) {
  .articulo-title h1 {
    margin-left: 50px;
  }
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.entry-content img,
.entry-content iframe,
.entry-content object,
.entry-content embed {
  max-width: 100%;
}

ol li {
  line-height: 1.5em;
}

.wp-embed {
  max-width: 80%;
}

.iframe-flexible-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
}

.iframe-flexible-container iframe,
.iframe-flexible-container object,
.iframe-flexible-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100%;
}
